// .class--classname BEM
//.class-size eg .sfFormcontrol-sm, .sfFormcontrol-lg
.cd_links_title {
    padding: 20px 20px 10px;
    font-weight: $font-weight-semibold;
    font-size: 1.5rem;
    display: block;
}

header {
    &.documentation {
        position: sticky;
        top: 0;
        z-index: 1;
        background: #5253f3;
        color: #fff;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    h1 {
        font-weight: $font-weight-light;
        text-align: center;
        font-size: 1.75rem;
    }
}

.cd_content {
    margin-bottom: 40px;
}

.cd_sidenav {
    li {
        a {
            padding: 10px 20px;
            display: block;
            color: $gray-700;
        }
    }
    overflow: auto;
    height:calc(100% - 125px);
}

.cd_sidebar {
    height: 100vh;
    border-right: 2px solid $gray-200;
    padding-right: 0;
    background: $gray-100;
    position: fixed;
    width: 20%;
    padding: 0 !important;
}

.cd_preview {
    margin-left: 16.666%;
    .content_title {
        font-weight: 300;
        font-size: 3.5rem;
        margin-bottom: 15px;
    }
    .content_lead {
        margin-bottom: 20px;
        font-weight: 300;
        font-size: 1.4rem;
    }
    .example_title {
        display: block;
        font-weight: 500;
        font-size: 1.85rem;
        margin-bottom: 20px;
    }
}

.cd_list {
    ul {
        li {
            list-style: disc;
            list-style-position: inside;
            margin-bottom: 5px;
        }
    }
}

pre {
    font-size: 85%;
}

.codeContent {
    span {
        line-height: 1.5
    }
}

.cd_example {
   
    &-bg {
        background: $gray-200;
        .modal {
            position: relative;
            top: auto;
            right: auto;
            bottom: auto;
            left: auto;
            z-index: 1;
            display: block;
        }
    }
    &.no-bg {
        .preview,
        .preview [class^=sfCol-] {
            border: none;
        }
    }
    .preview {
        // border: 1px solid $gray-200;
        // padding: 15px;
        .custom-tooltip  {
            margin-bottom:25px;
        }
        .color-white {
            background: $gray-800;
        }
        .gray-100 {
            background: $gray-100; 
        }
        &.badges {
            >* {
                margin-bottom: 20px;
            }
        }
        .pagination {
            margin-left: 0;
        }
        &.flexvalue {
            &.spacing {
                >* {
                    padding: 15px;
                }
            }
            &.height {
                >* {
                    height: 200px;
                }
            }
            >* {
                border: 1px solid rgba($danger, 0.25);
                background: rgba($danger, 0.025);
                &:not(:last-child) {
                    margin-bottom: 15px;
                }
                >div {
                    border: 1px solid rgba($danger, 0.25);
                    background: rgba($danger, 0.025);
                    padding: 15px;
                }
            }
        }
        .list-group {
            &:not(.list-group-hz) {
                max-width: 350px;
            }
        }
        &.tooltips {
            display: flex;
            justify-content: space-around;
            margin-top: 50px;
            &-margin {
                >button {
                    margin-bottom: 25px;
                    display: block;
                }
            }
        }
        
        &.grid {
            [class^=sfCol-],
            .sfCol {
                margin-bottom: 10px;
                p {
                    background-color: rgba($green, .8);
                    padding: 15px;
                    border-radius: 5px;
                    color: #fff;
                    text-align: center;
                }
            }
        }
        &.text-align {
            p {
                padding: 15px;
                border: 1px solid rgba($warning, .25);
                border-radius: 5px;
                margin-bottom: 20px;
            }
        }
        &.input-size {
            .sfFormcontrol {
                &:not(:last-child) {
                    margin-bottom: 20px
                }
            }
        }
        &.table {
            width: 100%;
            max-width: 100%;
            margin-bottom: 1rem;
            tr {
                >td,
                >th {
                    padding: .75rem;
                    border: 1px solid #dee2e6;
                }
                code {
                    color: $red;
                }
            }
        }
    }
    
    .sfContainer {
        padding: 15px;
        border: 1px solid rgba($green, .25);
        border-radius: 5px;
    }
    &-links {
        a {
            display: block;
            margin-bottom: 10px;
        }
    }
    &-grays,
    &-colors {
        p {
            margin-bottom: 10px;
            display: block;
        }
    }
    &-filter {
        .sfButtonwrapper {
            margin-bottom: 0;
        }
    }
}